.special-box{
    width: 78%;
    border: 2px solid #DE4313;
    margin: auto;
     padding: 5px 25px;
     background: linear-gradient(to bottom right, #f3b09a, #efe3d1);
     color: #DE4313;
     border-radius: 8px;
     font-size: 15px;
     font-family: "Roboto";
     font-weight: 400;
}

.flex-box-special{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.flex-box-special h4,.flex-box-special p{
    padding: 0;
    margin: 0;
}

.special-box img{
    width: 25px;
    height: 25px;
}

.flex-start{
    display: flex;
    align-items: center;
    gap: 8px; 
    margin-top: 5px;
}

.flex-start p{
   padding: 0;
   margin: 0;
}