.offer-box{
    width: 78%;
    margin: auto;
    background-color: white;
    
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    padding: 11px 25px;
    margin-bottom: 10px;
}

.offer-box-selected{
        border: 3px solid #47BA68;
        background-color: #e7fbee;
}

.offer-status{
    background-color: #47BA68;
    position:absolute;
    left: 16%;
    top: 0;
    padding: 0px 23px;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    color:  white;
    font-size: 10px;
}

/* .offer-status p{
padding: 0;
margin: 0;
} */


.radio-button{
    width: 25px;
    height: 25px;
    border: 3px solid #BEBEBE;
    border-radius: 50%;
}

.radio-button-image{
    width: 27px;
    height: 27px;
    /* border: 3px solid #BEBEBE;
    border-radius: 50%; */
    background-image: url(../assets/radio.png);
    background-size: cover;
}

.left-offer{
    display: flex;
    align-items: center;
    justify-content: space-around;
    /* width: 70%; */
    gap: 10px;
}
.offer-text,.total-money{
    font-family: "Roboto";
    font-weight: 500;
    font-size: 16px;
    padding: 0;
    margin: 0;
}

.month-money{
    font-size: 10px;
    font-weight: 400;
    padding: 0;
    margin: 0;
}

.money-span{
    font-size: 12px;
}
.money-span-month{
    font-size: 12px;
    font-weight: 500;
}